import React from "react";
import Card from "@material-ui/core/Card";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import CompareChart from './CompareChart'
import ProgramInfoButton from './ProgramInfoButton';
import "./css/subject.css";
import "./css/global.css"

const toDollars = (x) => `$${x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}`;

const styles = {
  title: {
    textAlign: 'center'
  },
  programLink: {
    fontSize: 'small',
    textDecoration: 'none'
  }
};

let ProgramList = (props) => {
  return (
    <div>
      {props.programs.programs.map(program => (
        <CompareCard key={program.id} {...program} />
      ))}
    </div>
  );
}

let CompareCard = (props) => {
  const program = props;
  return (
    <div className='compareCard'>
      <Paper>
        <Card>
          <div className='compareCardContent'>
            <h2 style={styles.title}>{program.school}</h2>
            <h3 style = {styles.title} className='programTitle'>{program.title}</h3>
            <Grid container spacing={1}>
              <Grid item xs = {7}>
                <Grid container>
                  <Grid item xs={6}>
                    <h4 className='programListHeader'>Online</h4>
                    <ul className='noBullet'>
                      <li>Total: {toDollars(program.total_online)}</li>
                      <li>Tuition: {program.online_tuition ? toDollars(program.online_tuition):"N/A"}</li>  
                      <li>Fees: {program.online_fees ? toDollars(program.online_fees): "N/A"}</li>
                      <ProgramInfoButton info = {[program.online_notes, program.online_link]}/>

                    </ul>
                  </Grid>
                  <Grid item xs={6}>
                    <h4 className='programListHeader'>Campus</h4>
                    <ul className='noBullet'>
                      <li>Total: {toDollars(program.total_campus)}</li>  
                      <li>Tuition: {program.campus_tuition ? toDollars(program.campus_tuition):"N/A"}</li>  
                      <li>Fees: {program.campus_fees ? toDollars(program.campus_fees): "N/A"}</li>
                      <ProgramInfoButton info = {[program.campus_notes, program.campus_link]}/>
                    </ul>
                  </Grid>
                </Grid>
                <Grid container spacing={1}>
                  <Grid item xs={12}>
                    <p className='programSummary'>Total Projected Savings: {toDollars((program.total_campus - program.total_online).toFixed(2))}</p>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={5}>
                <CompareChart prices = {[program.total_online, program.total_campus]}/>
              </Grid>
            </Grid>
          </div>
        </Card>
      </Paper>
    </div>
  );
}

export default ProgramList;
