import React from 'react';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import Contact from '../Contact';
import Credits from '../Credits';

const useStyles = makeStyles(theme => ({
  fill: {
    backgroundColor: '#282C34',
  },
  pointer: {
    cursor: 'pointer',
  },
  textStyle: {
    color: '#09D3AC',
    textAlign: 'center'
  }
}));

let ContactPopup = () =>{
    const [open, setOpen] = React.useState(false);
    const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div>
       <p onClick={handleClickOpen}>
        Contact
      </p>
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
          <Contact/>
      </Dialog>
    </div>
  )
}

let Footer = () => {
    const classes = useStyles();
    let year = new Date().getFullYear();
    return (
      <Grid container
          direction="row"
          justify="center"
          alignItems="center"
          className = {classes.fill}>
        <Grid item xs={4}>
          <div className={[classes.textStyle, classes.pointer].join(' ')}><ContactPopup/></div> 
        </Grid>
        <Grid item xs={4}>
          <p className = {classes.textStyle}>{year} GradHack</p> 
        </Grid>
        <Grid item xs={4}>
          <div className={[classes.textStyle, classes.pointer].join(' ')}><Credits/></div>
        </Grid>
      </Grid>
    );
}

export default Footer;
