import React, { Fragment } from "react";
import { Link } from "react-router-dom";
import "./css/global.css";
// import SubjectLinkCard from "./SubjectLinkCard";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import titleIcon from './img/title.svg';
import accountingIcon from './img/accounting.svg';
import compsciIcon from './img/compsci.svg';
import publicHealthIcon from './img/publichealth.svg'
import dataScienceIcon from './img/datascience.svg'
import businessIcon from './img/business.svg';
import eceeIcon from './img/ecee.svg';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import ProgramList from './ProgramList'


const data = require("../data.json");

const styles = {
  mainCard: {
    margin: '2em',
    padding: '1em'
  },
  titleIcon: {
    marginLeft: '4em',
    width:'75%'
  },
  titleIconContainer: {
    marginTop: '3em',
    merginLeft: '3em'
  },
  subjectIcon:{
    width: '65%',
    marginTop: '15px'
  },
  font: {
    fontSize: 'smaller',
  },
  link: {
    textDecoration: 'none'
  }
};

const testSubjects = [
  {
    id: 1,
    name: "Computer Science",
    link: "/subjects/compsci"
  },
  {
    id: 2,
    name: "Accounting",
    link: "/subjects/accounting"

  },
  {
    id: 3,
    name: "Public Health",
    link: "./subjects/publichealth"
  },
  {
    id: 4,
    name: "Data Science",
    link: "/subjects/datascience"
  },
  {
    id: 5,
    name: "Business",
    link: "/subjects/business"
  },
  {
    id: 6,
    name: "ECEE",
    link: "/subjects/ecee"
  }
];

const image = (name) => {
  switch(name) {
    case 'Public Health':
      return publicHealthIcon
    case 'Accounting':
      return accountingIcon
    case 'Data Science':
      return dataScienceIcon
    case 'Computer Science':
      return compsciIcon
    case 'ECEE':
      return eceeIcon
    case 'Business':
      return businessIcon
    default:
      return titleIcon
  }
};

const useStyles = makeStyles({
  root: {
    background: '#FF8E53',
    borderRadius: 3,
    border: 0,
    color: 'white',
    height: 48,
    padding: '0 30px',
    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
    textDecoration: 'none'
  },
  label: {
    textTransform: 'capitalize'
  },
  noUnderline :{ 
    textDecoration: 'none'
  }
});

const SubjectOptionsList = (props) => {
    const classes = useStyles();
  return (
    <Fragment>
      <GridList cols={3}>
        {testSubjects.map(tile => (
          <GridListTile key={tile.id} cols={1}>
            <Paper style={styles.mainCard}>
              <Grid container direction="row" alignItems="center">
                <Grid item xs = {2}>
                  <img style={styles.subjectIcon} alt={tile.name} src={image(tile.name)}></img>
                </Grid>
                <Grid wrap="nowrap" item xs = {5}>
                  <h5>{tile.name}</h5>
                </Grid>
                <Grid item xs = {5}>
                   <Link to={tile.link} className={classes.noUnderline}>
                   <Button classes={{
                    root: classes.root, 
                    label: classes.label,
                  }} variant="contained" disableUnderline={false}>
                    Compare
                  </Button>
                  </Link>
                </Grid>
              </Grid>
            </Paper>
          </GridListTile>
        ))}
      </GridList>
    </Fragment>
  )
};

class Subjects extends React.Component {
  render() {
    const { params } = this.props.match;
    if (params.title) {
      const subjectData = data['subjects'].find(s => s.name === params.title);
      return (
        <div className='Main'>
          <h1>{subjectData.display_title}</h1>
          <ProgramList programs = {subjectData}/>
        </div>
      );
    } else
      return (
        <div className='Main'>
          <Grid container>
            <Grid item md={12}>
              <SubjectOptionsList subjects={testSubjects}/>
            </Grid>
          </Grid>
        </div>
      );
  }
}
export default Subjects;
