import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogContent from '@material-ui/core/DialogContent';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  root: {
    opacity: 0.5,
    color: '#cc0000',
    height: 25,
    padding: 0
  }
});

const DialogContent = withStyles(theme => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

let ProgramInfoButton = (props) => {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const classes = useStyles();

  
const styles = {
  programLink: {
    fontSize: 'small',
    textDecoration: 'none',
    display: 'block',
    marginTop: '1em'
  }
};

  let notes = props.info[0];
  let link = props.info[1];
  return (
     notes.length > 4 ? 
    <div>
       <Button classes = {{root: classes.root}} variant="outlined" disableUnderline={true}
                  onClick={handleClickOpen}>
        notes
      </Button>
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <DialogContent dividers>
          <Typography gutterBottom>
           {notes}
           <a style = {styles.programLink} href = {link} target="_blank" rel="noopener noreferrer">Program Site</a>
          </Typography>
        </DialogContent>
      </Dialog>
    </div>
    :
    <div></div>
  );
}

export default ProgramInfoButton;