import React, { Fragment } from "react";
import './css/global.css';
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper"
import { makeStyles } from '@material-ui/core/styles';
import messageIcon from './img/message.svg'

const useStyles = makeStyles(theme => ({
  mainCard: {
    margin: '1em',
    padding: '1em',
    marginTop: '-15vh'
  },
  cardText: {
    fontSize: 'medium'
  },
  link: {
    color: '#FF8559',
    textDecoration: 'none'
  },
  icon: {
    marginLeft: '4em',
    width:'65%'
  }
}));

function AboutCard(){
  const classes = useStyles();
  return (
    <Fragment>
      <Paper className={classes.mainCard}>
        <Grid container justify="center">
          <Grid item xs = {1}/>
          <Grid item xs={7}>
            <h2>Contact</h2>
            <p>
              GradHack is currently a work in progress. As such, there is a lot of room for improvements, contributors, and feedback. If you have any comments or interest in advancing the work of GradHack, we'd love for you to <a href="mailto:philson@gatech.edu">contact us</a>.
            </p>
          </Grid>
          <Grid item xs ={3}>
            <img className={classes.icon} src={messageIcon} alt="Contact"/>
          </Grid>
        </Grid>
      </Paper>
    </Fragment>
  );
}


class Contact extends React.Component {
  render() {
    return (
      <div className = "Main">

        <AboutCard></AboutCard>
      </div>
    )
  }
}
export default Contact