import React, { Fragment } from "react";
import { Link } from "react-router-dom";
import { makeStyles } from '@material-ui/core/styles';
import "./css/global.css";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import titleIcon from './img/title.svg';
import accountingIcon from './img/accounting.svg';
import compsciIcon from './img/compsci.svg';
import publicHealthIcon from './img/publichealth.svg'
import dataScienceIcon from './img/datascience.svg'
import businessIcon from './img/business.svg';
import eceeIcon from './img/ecee.svg';
import Button from '@material-ui/core/Button';

const testSubjects = [
  {
    id: 1,
    name: "Computer Science",
    link: "/subjects/compsci"
  },
  {
    id: 2,
    name: "Accounting",
    link: "/subjects/accounting"

  },
  {
    id: 3,
    name: "Public Health",
    link: "./subjects/publichealth"
  },
  {
    id: 4,
    name: "Data Science",
    link: "/subjects/datascience"
  },
  {
    id: 5,
    name: "Business",
    link: "/subjects/business"
  },
  {
    id: 6,
    name: "ECEE",
    link: "/subjects/ecee"
  }
];

const imageToIcon = (name) => {
  switch(name) {
    case 'Public Health':
      return publicHealthIcon
    case 'Accounting':
      return accountingIcon
    case 'Data Science':
      return dataScienceIcon
    case 'Computer Science':
      return compsciIcon
    case 'ECEE':
      return eceeIcon
    case 'Business':
      return businessIcon
    default:
      return titleIcon
  }
};

const useStyles = makeStyles({
  root: {
    background: '#FF8E53',
    borderRadius: 3,
    border: 0,
    color: 'white',
    height: 48,
    width: '12vw',
    padding: '0 30px',
    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
  },
  label: {
    textTransform: 'capitalize'
  },
  noUnderline: {
    textDecoration: 'none'
  },
  subjecTitle: {
    fontSize: 'larger'
  },
  mainCard: {
    margin: '2em',
    padding: '1em'
  },
  titleIcon: {
    marginLeft: '4em',
    marginTop: '-2em',
    width:'65%'
  },
  titleIconContainer: {
    marginTop: '3em',
    merginLeft: '3em'
  },
  subjectIcon:{
    width: '65%',
    marginTop: '15px'
  },
  font: {
    fontSize: 'smaller',
  },
  link: {
    textDecoration: 'none'
  }
});

const SubjectOptionsList = (props) => {
  const classes = useStyles();
  return (
    <Fragment>
      <GridList cols={2}>
        {testSubjects.map(tile => (
          <GridListTile key={tile.id} cols={1}>
            <Paper className={classes.mainCard}>
              <Grid container justify="center" alignItems="center">
                <Grid item xs = {2}>
                  <img className={classes.subjectIcon} src={imageToIcon(tile.name)} alt=""></img>
                </Grid>
                <Grid item xs = {5}>
                  <h4 className={classes.subjecTitle}>{tile.name}</h4>
                </Grid>
                <Grid item xs = {5}>
                   <Link to={tile.link} className={classes.noUnderline}>
                   <Button classes={{
                    root: classes.root, 
                    label: classes.label,
                  }} variant="contained">
                    Compare
                  </Button>
                  </Link>
                </Grid>
              </Grid>
            </Paper>
          </GridListTile>
        ))}
      </GridList>
    </Fragment>
  )
};

function MainCard(){
  const classes = useStyles();
  return (
    <Fragment>
      <Paper className={classes.mainCard}>
        <Grid container justify="center">
          <Grid item xs = {1}/>
          <Grid item xs={7} className={classes.mainText}>
            <h2>Masters degrees, on the cheap.</h2>
            <p className={classes.font}>Interested in a masters degree but turned off by tuition?</p>
            <p className={classes.font}>Advancements in educational technology have resulted in a wide offering of low cost masters programs from prestigious American universities. These provide the same degrees and content as their on campus program at a fraction of the cost.</p>
            <p className={classes.font}>Explore GradHack's continuously update list of online offerings, and see if your next journey in higher education could be online. </p>
          </Grid>
          <Grid item xs ={3} className={classes.titleIconContainer}>
            <img className={classes.titleIcon} src={titleIcon} alt=""/>
          </Grid>
        </Grid>
      </Paper>
    </Fragment>
  );
}

function Home() {
  return (
    <div className="Main">
      <Grid container>
        <Grid item xs = {12}>
          <MainCard/>
        </Grid>
        <Grid item xs={12}>
          <SubjectOptionsList subjects={testSubjects}/>
        </Grid>
      </Grid>
    </div>
  );
}

export default Home;
