import React from 'react';
import { Route, Link, BrowserRouter as Router, Switch } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';
import { Grid} from '@material-ui/core';
import Home from '../Home';
import About from '../About';
import Subjects from '../Subjects';
import Contact from '../Contact';

const useStyles = makeStyles(theme => ({
  textStyle: {
    textDecoration: 'none',
    color: '#09D3AC',
    textAlign: 'center',
  },
  logoText: {
    textDecoration: 'none',
    color: '#09D3AC',
    textAlign: 'center',
    paddingTop: '15px',
    fontSize: '50px',
    marginTop: '15px'
  },
  margins: {
    marginLeft: '2em'
  },
  fill: {
    backgroundColor: '#282C34',
    marginTop: '-1.7vw'
  },
  alignCenter: {
    textAlign: 'center', 
    fontSize: '1.7em'
  }
}));

let Header = () => {
  const classes = useStyles();
  return (
      <Grid className = {classes.fill}>
        <Router>
          <h1 className={classes.logoText}>GradHack</h1>
          <div className = {classes.alignCenter}>
            <Link to="/" className={classes.textStyle}>Home</Link>
            <Link to="/about" className={`${classes.margins} ${classes.textStyle}`}>About</Link>
            <Link to="/subjects" className={`${classes.margins} ${classes.textStyle}`}>Subjects</Link>
            <Link to="/contact" className={`${classes.margins} ${classes.textStyle}`}>Contact</Link>
          </div>
            <Switch>
                <Route exact path="/" component={Home} />
                <Route path="/about" component={About} />
                <Route path="/subjects/:title?" component={Subjects} />
                <Route path="/contact" component={Contact}/>
            </Switch>
        </Router>
      </Grid>
    );
}

export default Header;
