import React, { Fragment } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Paper from "@material-ui/core/Paper"
import './css/global.css';

const useStyles = makeStyles(theme => ({
  mainCard: {
    margin: '4em',
    padding: '1em'
  },
  cardText: {
    margin: '20px',
    fontSize: 'small'
  },
  titleText: {
    margin: '20px',
    fontSize: 'medium',
    fontWeight: '600'
  },
  link: {
    color: '#FF8559',
    textDecoration: 'none'
  }
}));

let AboutCard = () => {
  const classes = useStyles();
  return (
    <Fragment>
      <Paper className={classes.mainCard}>
        <p className={classes.titleText}>America's Student Loan Debt Problem</p>
        <p className={classes.cardText}>
          Today America’s outstanding student loan debt sits at approximately <a href = "https://www.marketwatch.com/story/student-debt-just-hit-15-trillion-2018-05-08" className={classes.link} target="_blank" rel="noopener noreferrer">$1.5 trillion dollars</a>, and it shows no significant sign of abating. Tuition has been rising at twice the <a href = "https://www.edvisors.com/plan-for-college/saving-for-college/tuition-inflation/" className={classes.link} target="_blank" rel="noopener noreferrer">consumer inflation rate</a>, leading to student loans accounting for the majority of American debt that is not housing. 
        </p>
        <p className={classes.cardText}>
          While student loans are not necessarily a bad thing, we should also recognize that for many, the end has not justified the debt ridden means. Research has found that, among other things, student loan debt is associated with a variety of negative life consequences including <a href="https://www.demographic-research.org/volumes/vol30/69/" className={classes.link} target="_blank" rel="noopener noreferrer">lower marriage rates</a>, <a href="https://www.philadelphiafed.org/-/media/research-and-data/publications/working-papers/2015/wp15-26.pdf" className={classes.link} target="_blank" rel="noopener noreferrer">lower small business formation</a>, and high levels of <a href="https://www.meetsummer.org/share/Summer-Student-Debt-Crisis-Buried-in-Debt-Report-Nov-2018.pdf" className={classes.link} target="_blank" rel="noopener noreferrer">stress</a>. It should also be mentioned that this stress is <a href = "https://www.ncbi.nlm.nih.gov/pubmed/29952580" className={classes.link} target="_blank" rel="noopener noreferrer"> more acutely felt</a> by historically disadvantaged minorities.
        </p>
        <p className={classes.titleText}>Massive Cost Savings Through MOOCs</p>
        <p className={classes.cardText}>
          Luckily, educational technology is offsetting the geographic and financial constraints of the higher education experience. Distance learning through online classes is a viable alternative which offers the same curricula and degree and many on campus programs. This is especially the case in the realm of graduate degrees via online learning platforms called MOOCs (Massive Online Open Course). MOOC platforms have pivoted from simply providing condensed mini courses into offering full fledged master degrees which are equivalent to their on campus versions. They are generally much more affordable than traditional programs, and they produce the same learning outcomes based on <a href="http://www.davidjoyner.net/blog/toward-cs1-at-scale-building-and-testing-a-mooc-for-credit-candidate/" className={classes.link} target="_blank" rel="noopener noreferrer">recent research</a>. 
        </p>
        <p className={classes.cardText}>
          Although these new degree offerings offer potentially massive savings, their existence alone has no noticeable effect on lowering the aggregate student loan debt in America. One potential reason is that these programs are not siphoning away would be money borrowers. Instead, they tend to be preferentially adopted by mid career professionals. In other words, they aren’t lowering student loan debt. One <a href="https://www.educationnext.org/elite-grad-school-degree-goes-online-georgia-tech-virtual-masters-increase-access-education" className={classes.link} target="_blank" rel="noopener noreferrer">study</a> has found these types of programs are effectively offering an opportunity for adults who would otherwise not be enrolled in an academic program. 
        </p>
        <p className={classes.titleText}>What GradHack Wants To Do</p>
        <p className={classes.cardText}>
          The potential savings of online alternatives, are not officially limited to any particular student demographic. So GradHack aims to help graduate degree seekers find cost effective online alternatives to both expensive residential programs and <a href="https://www.marketwatch.com/story/for-profit-middlemen-may-be-driving-up-the-cost-of-online-higher-education-2019-09-19" className={classes.link} target="_blank" rel="noopener noreferrer">overpriced online programs</a> by presenting only those degree programs which are both reputable and leverage low cost educational platforms for content delivery. 
        </p>
        <p className={classes.cardText}> 
          It is our hope that by promoting the adoption of these MOOC based programs to a wider audience, we can help individuals make more informed choices and apply <a href="https://www.newamerica.org/post-secondary-national-policy-institute/our-blog/can-online-learning-bend-the-cost-curve-of-higher-education/" className={classes.link} target="_blank" rel="noopener noreferrer"> downward pressure</a> on the rising tide of tuition prices across America. 
        </p>
      </Paper>
    </Fragment>
  );
}

let About = () => {
    return (
      <div className = "Main">
        <AboutCard></AboutCard>
      </div>
    )
  
}
export default About