import React from "react";
import Highcharts from "highcharts";
import HighchartsReact from "highcharts-react-official";

let CompareChart = (props) => {
    const onlinePrice = props.prices[0];
    const campusPrice = props.prices[1];
    const options = {
      title: {
        text: null
      },
      credits: {
        enabled: false
      },
      tooltip: {
        enabled: false
      },
      colors: ["#09d3ac"],
      chart: {
        type: "column"
      },
      xAxis: {
        type: "category"
      },
      yAxis: {
        title: {
          text: "Price"
        }
      },
      legend: {
        enabled: false
      },
      series: [
        {
          data: [
            ["online", onlinePrice],
            ["campus", campusPrice]
          ]
        }
      ]
    };
    return (
      <div className="test">
        <HighchartsReact
          highcharts={Highcharts}
          containerProps={{ style: { height: "14em" } }}
          options={options}
        />
      </div>
    );
}

export default CompareChart;
